body, html {
  width: 100%;
  height: 100%;
}

.App {
  text-align: center;
  color: #333333;
}

.App * {
  box-sizing: border-box;
}

h1 {
  font-size: 4em;
}

h2 {
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

input[type="text"], input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 19px;
  outline: none;
  border: 1px solid #f6405f;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  text-align: center;
}

.headLine {
  width: 100%;
  color: #f6405f;
}

.headLine span {
  font-size: 10px;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.suggestions {
  width: 250px;
  align-self: center;
  margin: 0;
  font-size: 14px;
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:first-child {
  border-top-left-radius: 19px;
  border-top-right-radius: 19px;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 19px;
  border-bottom-right-radius: 19px;
}

.suggestions > li:hover {
  background-color: #BCDCDB;
}

.event {
  padding: 10px 10px 20px;
  margin: 10px auto;
  text-align: left;
  position: relative;
  background-color: rgba(1, 69, 93,1);
  max-width: 800px;
  color: white;
}

.event__Overview--data {
  display: flex;
  flex-direction: row;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  font-size: 14px;
  border-radius: 15px;
  letter-spacing: 2px;
  border: none;
  outline: none;
  outline-style: none;
  cursor: pointer;
  background-color: #f6405f;
  color: white;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going, .visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 100%;
  margin: 20px 0 50px;
  display: flex;
  flex-direction: column;
}

.alert {
  height: 30px;
}
